.container{
    width: 100%;
    height: 663px;
    display: flex;
    flex-direction: column;
}

h2{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    color: #303F9F;
}

li{
    font-family:  'Lucida Sans Regular', 'Lucida Sans', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 16px;
    padding: 10px 5px;
    margin: 10px 5px;
    list-style-type: none;
    float:left;
    clear:left;
}

.titulo{
    width: 100%;
    height: 12%;
    text-align: center;
    justify-content: center;
}

.filtro-portfolio{
    width: 100%;
    height: 12%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.class-projetos{
    display: grid;
    gap: 2%;

}

.projeto{
    height: 200px;
    width: 250px;
    background-color: aqua;
}